#root {
  height: 100%;
}

.wrapper {
  background-color: #000000;
  position: relative;
  min-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  @supports (overflow: clip) {
    overflow: clip;
  }
  > main {
    flex: 1 1 auto;
  }
  > * {
    min-width: 0;
  }
}

