// variables
$fontFamily: "Grandstander";
$mainColor: #ffffff;
$minWidth: 320;
$maxWidth: 1920;
$mobile: 1399.98px;
$desktop: 1919.98px;

// null
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
:focus,
:active {
  // outline: none;
}
a:focus,
a:active {
  // outline: none;
}
html,
body {
  height: 100%;
  // min-width: $minWidth + px;
}
body {
  color: $mainColor;
  line-height: 1;
  font-family: $fontFamily;
  font-size: 14px;
  //text-rendering: optimizeLegibility;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body::-webkit-scrollbar {
  width: 0.3125rem;
}

body::-webkit-scrollbar-track {
  background: rgba(15, 4, 36, 0.726);
}

body::-webkit-scrollbar-thumb {
  background-color: linear-gradient(146deg, #decaff 0%, #691ee2 100%);
  border-radius: 1.25rem;
  border: 0.1875rem solid #691ee275;
}
input,
button,
textarea {
  font-family: $fontFamily;
  font-size: inherit;
  line-height: inherit;
}
button {
  cursor: pointer;
  color: inherit;
  background-color: transparent;
}
a {
  color: inherit;
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

// Common..
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.soundToggler {
  &__itemOn {
    display: flex;
    button {
      width: 3.125rem;
      height: 3.125rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 3.75rem;
      border: 0.125rem solid #fff;
      transition: 0.25s;
      svg {
        path {
          transition: 0.25s;
        }
        @media (max-width: $mobile) {
          height: 0.875rem;
          width: 1.125rem;
        }
      }
      @media (max-width: $mobile) {
        height: 2.1875rem;
        width: 2.1875rem;
      }
    }
    button:hover {
      background-color: #ffffff;
      transition: 0.25s;
      svg {
        path {
          stroke: #000000;
          transition: 0.25s;
          @media (max-width: $mobile) {
            stroke: #ffffff;
          }
        }
      }
      @media (max-width: $mobile) {
        background-color: transparent;
      }
    }
  }
  &__itemOff {
    display: flex;
    button {
      width: 3.125rem;
      height: 3.125rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 3.75rem;
      border: 0.125rem solid #fff;
      transition: 0.25s;
      svg {
        path {
          transition: 0.25s;
        }
        @media (max-width: $mobile) {
          height: 0.875rem;
          width: 1.125rem;
        }
      }
      @media (max-width: $mobile) {
        height: 35px;
        width: 35px;
      }
    }
    button:hover {
      background-color: #ffffff;
      transition: 0.25s;
      svg {
        path {
          transition: 0.25s;
          stroke: #000000;
          @media (max-width: $mobile) {
            stroke: #ffffff;
          }
        }
      }
      @media (max-width: $mobile) {
        background-color: transparent;
      }
    }
  }
}

._hide {
  display: none !important;
}
._active {
  display: flex !important;
}
